import Swiper from 'swiper';

$('.js-slider').each((index, item) => {
	let $component = $(item);
	let $backSlider = $component.find('.js-back-slider');
	let $frontSlider = $component.find('.js-front-slider');
	let $sliderUI = $component.find('.js-slider-ui');
	let sliderTimeout;

	let backSlider = new Swiper($backSlider[0], {
		speed: 500,
		spaceBetween: 0,
		slidesPerView: 1,
	});

	let frontSlider = new Swiper($frontSlider[0], {
		speed: 300,
		spaceBetween: 0,
		slidesPerView: 1,
		autoplay: true,
		control: backSlider,
	});

	$sliderUI.find('.js-arrow-prev').on('click', () => {
		frontSlider.slidePrev();
	});

	$sliderUI.find('.js-arrow-next').on('click', () => {
		frontSlider.slideNext();
	});

	frontSlider.on('slideChange', () => {
		if (sliderTimeout) {
			clearTimeout(sliderTimeout);
		}

		sliderTimeout = setTimeout(() => {
			backSlider.slideTo(frontSlider.activeIndex);
		}, 300);
	});

	backSlider.on('slideChange', () => {
		if (sliderTimeout) {
			clearTimeout(sliderTimeout);
		}

		sliderTimeout = setTimeout(() => {
			frontSlider.slideTo(backSlider.activeIndex);
		}, 300);
	});
});
